.greetingForm {


}

.inputAndButtonContainer {
    position: relative;
    display: flex;
    gap: 36px;
    margin-bottom: 10px;
}

.error {
    color: red;
}

.input {
    min-width: 370px;
    border: 1px solid rgba(209, 209, 209, 1);
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    padding: 8px 12px;
    margin-bottom: 10px;
}

.input:focus {
    outline: none;
}

.errorInput {
    border: 1px solid red;

}

.button {
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    text-transform: capitalize;
    padding: 8px 24px;
    border: none;
    border-radius: 3px;
    color: white;
    background-color: rgba(0, 102, 204, 0.9);
    max-height: 40px;
    transition: background-color 0.3s ease-in-out;
}

.button:focus {
    background-color: rgba(0, 102, 204, 1);
}

.button:active {
}

.button:disabled {
    background-color: rgba(0, 102, 204, 0.5);
}

.text {
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 10px;
}

.greeting {
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    margin-bottom: 100px;
}
